import React from "react"
import styled from "styled-components";

const AboutMeContainer = styled.div`
  display: flex;
  justify-content: center;
`

const ProfileContainer = styled.div`
  width: 800px;
  min-width: 300px;
  margin: 0 20px;
  padding: 40px;
  background-color: whitesmoke;
  text-align: center;
  position: relative;
  top: 50px;
  border-radius: 20px;
`;

const ProfilePhoto = styled.div`
  img {
    min-width: 100px;
    width: 100px;
    height: 100px;
    border-radius: 50%;
  }
`;

const ProfileTitle = styled.div``

const ProfileContent = styled.div`
  margin-top: 10px;
  white-space: pre-line;
`;

const AboutPage = () => {
  return (
    <AboutMeContainer>
      <ProfileContainer>
        <ProfilePhoto>
          <img
            src="data:image/jpeg;base64,"
            alt=""
          />
        </ProfilePhoto>
        <ProfileTitle>
          <h2>Dylan Lo</h2>
        </ProfileTitle>
        <ProfileContent>
          <p>前往前端工程師的路上</p>
          <p>
            希望打造一個大家可以互相討論分享酒譜與可以買到自己想要的酒的地方
          </p>
        </ProfileContent>
      </ProfileContainer>
    </AboutMeContainer>
  );
};

export default AboutPage;